.postContent {
    > h2 {
        @apply text-2xl font-bold mb-5;
    }

    > h3 {
        @apply text-xl font-bold mb-4;
    }

    > p,
    > ol,
    > ul {
        @apply mt-4;

        & + h2 {
            @apply mt-8;
        }

        & + h3 {
            @apply mt-6;
        }
    }

    > ol,
    > ul {
        @apply mb-4 pl-9;
    }

    > ol {
        @apply list-decimal;
    }

    > ul {
        @apply list-disc;
    }

    a {
        text-decoration: underline;
        color: #43b2ff;
    }
}

.aside {
    @apply w-[295px] min-w-[295px] mobile:w-full mobile:max-w-none mr-10 mobile:mr-0 mobile:mt-[60px] mobile:order-2;

    .asideSticky {
        @apply sticky top-0;
    }

    &Title {
        @apply uppercase mobile:text-xl font-medium;
    }

    .postsCategories {
        @apply my-6;

        li {
            & + li {
                @apply mt-2;
            }
        }

        button {
            @apply w-full p-4 rounded-lg border border-black font-medium text-left;
        }

        .activeCategory {
            @apply bg-[#2A3266] text-white;
        }
    }

    .postsLinks {
        li {
            & + li {
                @apply mt-4;
            }
        }

        a {
            @apply flex justify-between bg-white rounded-lg shadow-popUp p-4 font-semibold;
        }

        svg {
            @apply min-w-[24px] ml-1;
        }
    }
}
